.navbar {
	display: flex;
	justify-content: space-around;
	padding: 16px;
	/* box-shadow: 0 5px 10px -2px black; */
	box-shadow: 0 1px 3px -2px black;
}

.navbar-logo{
	display: flex;
	align-items: center;
	gap: 10px;
	text-decoration: none;
}

.navbar-logo p {
	color: #171717;
	font-size: 38px;
	text-decoration: none;
}

.navbar-menu {
	display: flex;
	align-items: center;
	list-style: none;
	gap: 50px;
	color: #626262;
	font-size: 20px;
	font-weight: 500;
}

.navbar-menu li {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	gap: 3px;
	cursor: pointer;
	color: yellow;
}

.navbar-menu hr {
	border: none;
	width: 80%;
	height: 3px;
	border-radius: 10px;
	background: #ff4141;
}

.navbar-login-cart {
	display: flex;
	align-items: center;
	gap: 45px;
}

.navbar-login-cart button {
	width: 157px;
	height: 58px;
	outline: none;
	border: 1px solid #7a7a7a;
	border-radius: 75px;
	color: #515151;
	font-size: 20px;
	font-weight: 600;
	background: white;
	cursor: pointer;
}

.navbar-login-cart button:active {
	background: #f3f3f3;
}

.navbar-login-cart .navbar-cart-count {
	width: 22px;
	height: 22px;
	display:flex;
	justify-content: center;
	align-items: center;
	margin-top: -35px;
	margin-left: -55px;
	border-radius: 11px;
	font-size: 14px;
	background: red;
	color: white;
}



